<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-date"></i> </el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="content-title"></div>
            <div class="plugins-tips">
                Element UI
                <a href="http://element.eleme.io/#/zh-CN/component/upload" target="_blank">Element UI Upload</a>
            </div>
            <el-upload class="upload-demo" drag multiple  list-type="picture-card" action="https://jsonplaceholder.typicode.com/posts/" :file-list="fileList" :on-change="handleChange">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em></em></div>
                <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
            </el-upload>
            <div class="content-title"></div>
            <div class="plugins-tips">
                vue-cropperjs cropperjs  Vue 
                <a href="https://github.com/Agontuk/vue-cropperjs" target="_blank">vue-cropperjs</a>
            </div>
            <div class="crop-demo">
                <img :src="cropImg" class="pre-img">
                <div class="crop-demo-btn">
                    <input class="crop-input" type="file" name="image" accept="image/*" @change="setImage"/>
                </div>
            </div>
            <el-dialog title="" :visible.sync="dialogVisible" width="30%">
                <vue-cropper ref="cropper" :src="imgSrc" :ready="cropImage" :zoom="cropImage" :cropmove="cropImage" style="width: 100%; height: 300px">
                <span slot="footer" class="dialog-footer">
                    <el-button @click="cancelCrop"> </el-button>
                    <el-button type="primary" @click="dialogVisible = false"> </el-button>
                </span>
                </vue-cropper>

            </el-dialog>
        </div>
    </div>
</template>

<script>
    import VueCropper from 'vue-cropperjs';
    import 'cropperjs/dist/cropper.css';
    export default {
        components: {VueCropper},
        name: 'upload',
        data() {
            return {
                fileList:[
                    {
                        name: 'food.jpeg',
                        url: 'https://www.baidu.com/img/bd_logo1.png'
                    },{
                        name: 'food2.jpeg',
                        url: 'https://www.baidu.com/img/bd_logo1.png'
                    }
                ],
                defaultSrc: '/static/img/img.jpg',
                imgSrc: '',
                cropImg: '',
                dialogVisible: false
            }
        },
        methods:{
            handleChange(file, fileList){
                console.log(file);
                console.log(fileList);
            },
            setImage(e){
                const file = e.target.files[0];
                if (!file.type.includes('image/')) {
                    return;
                }
                const reader = new FileReader();
                reader.onload = (event) => {
                    this.dialogVisible = true;
                    this.imgSrc = event.target.result;
                    this.$refs.cropper && this.$refs.cropper.replace(event.target.result);
                };
                reader.readAsDataURL(file);
            },
            cropImage () {
                this.cropImg = this.$refs.cropper.getCroppedCanvas().toDataURL();
            },
            cancelCrop(){
                this.dialogVisible = false;
                this.cropImg = this.defaultSrc;
            },
            imageuploaded(res) {
                console.log(res)
            },
            handleError(){
                this.$notify.error({
                    title: '',
                    message: ''
                });
            }
        },
        created(){
            this.cropImg = this.defaultSrc;
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .content-title{
        font-weight: 400;
        line-height: 50px;
        margin: 10px 0;
        font-size: 22px;
        color: #1f2f3d;
    }
    .pre-img{
        width: 100px;
        height: 100px;
        background: #f8f8f8;
        border: 1px solid #eee;
        border-radius: 5px;
    }
    .crop-demo{
        display: flex;
        align-items: flex-end;
    }
    .crop-demo-btn{
        position: relative;
        width: 100px;
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
        margin-left: 30px;
        background-color: #409eff;
        color: #fff;
        font-size: 14px;
        border-radius: 4px;
        box-sizing: border-box;
    }
    .crop-input{
        position: absolute;
        width: 100px;
        height: 40px;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
    .el-upload-dragger{
        width: 146px;
        height: 146px;
    }
</style>



